import React from "react";
import { Button, Comment, Form, Header, Rating } from "semantic-ui-react";
const Comments = () => {
  return (
    <Comment.Group>
      <Header as="h3" dividing>
        Client's Feedbacks
      </Header>

      <Comment>
        <Comment.Avatar src="https://react.semantic-ui.com/images/avatar/small/matt.jpg" />
        <Comment.Content>
          <Comment.Author as="a">Matt</Comment.Author>
          <Rating disabled icon="star" defaultRating={5} maxRating={5} />
          <Comment.Metadata>
            <div>Today at 5:42PM</div>
          </Comment.Metadata>
          <Comment.Text>How artistic!</Comment.Text>
          <Comment.Actions>
            <Comment.Action>Reply</Comment.Action>
          </Comment.Actions>
        </Comment.Content>
      </Comment>

      <Comment>
        <Comment.Avatar src="https://react.semantic-ui.com/images/avatar/small/elliot.jpg" />
        <Comment.Content>
          <Comment.Author as="a">Elliot Fu</Comment.Author>
          <Rating icon="star" disabled defaultRating={5} maxRating={5} />

          <Comment.Metadata>
            <div>Yesterday at 12:30AM</div>
          </Comment.Metadata>
          <Comment.Text>
            <p>This has been very useful for my research. Thanks as well!</p>
          </Comment.Text>
          <Comment.Actions>
            <Comment.Action>Reply</Comment.Action>
          </Comment.Actions>
        </Comment.Content>
      </Comment>

      <Comment>
        <Comment.Avatar src="https://react.semantic-ui.com/images/avatar/small/joe.jpg" />
        <Comment.Content>
          <Comment.Author as="a">Joe Henderson</Comment.Author>
          <Rating icon="star" disabled defaultRating={5} maxRating={5} />

          <Comment.Metadata>
            <div>5 days ago</div>
          </Comment.Metadata>
          <Comment.Text>Dude, this is awesome. Thanks so much</Comment.Text>
          <Comment.Actions>
            <Comment.Action>Reply</Comment.Action>
          </Comment.Actions>
        </Comment.Content>
      </Comment>
      <Form reply>
        <Form.TextArea placeholder="Tell us your opinion" />
        <Button
          onClick={() => alert("I dont work yet")}
          content="Add Comment"
          labelPosition="left"
          icon="edit"
          color="teal"
        />
        <Rating icon="star" defaultRating={5} maxRating={5} />
      </Form>
    </Comment.Group>
  );
};

export default Comments;
